.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}





/*change primary blue to my website blue, other preferences*/
.nav-bar-pref .btn-outline-primary {
  color: #0052a4 !important;
  border-color: #0052a4 !important;
  border-width:2px !important;
  font-size:18px !important;
  border-radius:20px !important;
  font-weight:500 !important;
}

/*change primary blue to my website blue*/
.nav-bar-pref .btn-outline-primary:hover {
  color: white !important;
  background-color: #0052a4 !important;
}

/*the navbar stretches too wide on wide screens*/
.nav-bar-pref {
  background-color:#fff;
  margin: auto;
  height:75px;
  width:100vw;
  position:fixed;
  top:0;
  z-index:100;
  padding-bottom:5px;
}

.navbar {
  margin:auto;
  max-width:1000px;
  padding: 0rem 1.5rem !important;
  height:75px;
  padding-bottom:5px
}

/*logo is slightly off-centered vertically, this fixes it, and width*/
.my-logo {
  width:140px;
  padding-top:14px;
}

/*-----------------------------------------------------------------------------------------------------------------------------*/
/*---------------------------------------------------------------Title---------------------------------------------------------*/
/*-----------------------------------------------------------------------------------------------------------------------------*/

/*don't want the gray background, and text left-align by default*/
.title-container {
  background-color:white !important;
  text-align:center;
  padding:0px 10px;
}

#title h1 {
  font-size:45px;
  line-height:54px;
  font-weight:bold;
  color: #222327;
  letter-spacing:-1px;
}

#title p {  
  color: #222327;
  font-size:27px;
  line-height:45px;
  margin-top:18px;
  font-weight:400;
  letter-spacing:-1px;
}
#title a:hover{
  text-decoration: none;
}

/*avatar is smaller, needs a littls space*/
.avatar {
  height:180px;
  margin-top:30px;
}

/*title should be taller, and content centered*/
#title {
  height:100vh;
  padding-top:75px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

/*-----------------------------------------------------------------------------------------------------------------------------*/
/*---------------------------------------------------------------Profile-------------------------------------------------------*/
/*-----------------------------------------------------------------------------------------------------------------------------*/

/*profile switches to blue background*/
#profile {
  background-color: #0052a4;
  color:white;
  padding:54px 18px;
  text-align:center;
}

#profile .container, #profile .col, #profile .row {
  margin:0px auto;
  padding:0px;  
  max-width:1000px;
}

#profile a {
  color:#ffb30f;
}

#profile p a {
  font-weight:500;
}

/*too big and not enough spacing*/
#profile h1 {
  font-size:36px;
  line-height:54px;
  margin-bottom:18px;
  font-weight:800;
}

/*too small*/
#profile p {
  font-size:18px;
  line-height:36px;
  font-weight:300;
  margin-bottom:18px;
  letter-spacing:-.5px;
}

/*change primary blue to my website blue, other preferences*/
#profile .btn-outline-primary {
  color: #ffb30f !important;
  border-color: #ffb30f !important;
  border-width:2px !important;
  font-size:18px !important;
  border-radius:18px !important;
  font-weight:700 !important;
  padding:10px;
}

/*change primary blue to my website blue*/
#profile .btn-outline-primary:hover {
  color: #0052a4 !important;
  background-color: #ffb30f !important;
}

/*-----------------------------------------------------------------------------------------------------------------------------*/
/*---------------------------------------------------------------Skills--------------------------------------------------------*/
/*-----------------------------------------------------------------------------------------------------------------------------*/

/*skills box will lay over profile background*/
/*give border and box shadow on the bottom*/
#skills {
  margin:-27px auto auto;
  width:95%;
  max-width:1000px;
  border-radius:10px;
  background-color:white;
  text-align: center;
  border: 2px solid #0052a4;
  box-shadow: 0px 15px 10px -10px #888888;
}

/*size and center align the code icon*/
#skills .code-icon {
  height:90px;
  display:block;
  margin:36px auto;
}


/*too big and not enough spacing*/
#skills h1 {
  font-size:27px;
  line-height:36px;
  margin-bottom:18px;
  font-weight: bold;
}

#skills h2 {
  font-size:18px;
  line-height:36px;
  margin-bottom:18px;
  color: #0052a4;
}

/*paragraph font too small*/
#skills p {
  font-size:18px;
  line-height:36px;
  margin-bottom:18px;
}

/*remove bullets*/
#skills ul {
  list-style:none !important;
  margin: 0;
  padding: 0;
  font-size:18px;
  line-height:36px;
  margin-bottom:36px;
}


/*-----------------------------------------------------------------------------------------------------------------------------*/
/*---------------------------------------------------------------Works---------------------------------------------------------*/
/*-----------------------------------------------------------------------------------------------------------------------------*/

/*container*/
#works {
  text-align:center;
  margin: 54px 0px auto;
}

#works .container { 
  max-width:1000px;
}

/*"My Projects" header*/
#works h1 {
  font-size:36px;
  line-height:54px;
  letter-spacing:-.5px;
  font-weight:bold;
  margin:0px 36px 5px;
}

/*more space, spacing, size*/
.works-description {
  font-size:18px;
  line-height:36px;
  margin-bottom:36px; 
  width:90%;
  max-width:1000px;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

/*contains all cards*/
#works .card-deck {
  margin-right:0px;
  margin-left:0px;
}

/*each individual card*/
#works .card {
  max-width:500px;
  margin-bottom:36px;
  border-color: #0052a4;
  border-radius:10px;
  min-width:300px;  
  box-shadow: 0px 15px 10px -10px #888888;
}

/*holds each image*/
#works .card-img-container {
  width:95%;
  max-width:420px;
  max-height:300px;
  margin:18px auto;
  border-radius:10px;
  border:2px solid #cccccc;
  box-shadow: 0px 3px 2px -2px #888888;
  overflow:hidden;
}

#works img {  
  transition: transform .5s ease;
}

#works img:hover {
  transform: scale(1.5) rotate(-17deg);
}

/*everything below the image*/
#works .card-body {
  padding: 0px 1.25rem;
  margin-bottom:18px;
}

/*name of project*/
.card-app-name {
  font-size:20px;
  color: #0052a4;
  font-weight:500;
  margin-bottom:18px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;  
}

/*technology list, no specific height*/
.card-tech {
  font-size:18px;
  margin: 18px 0px;
  line-height:36px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

/*the actual summary subheader*/
#works .summary {  
  font-size:18px;
  color: #0052a4;
  font-weight:500;
  margin-bottom:18px;
}

/*summary of project, no specific height*/
#works .card-summary {
  font-size:14px;
  margin-bottom: 18px;
  line-height:36px;
}


/*change primary blue to my website blue, other preferences*/
#works .btn-outline-primary {
  color: #0052a4 !important;
  border-color: #0052a4 !important;
  border-width:2px !important;
  font-size:18px;
  border-radius:20px !important;
  font-weight:500 !important;
}

/*change primary blue to my website blue*/
#works .btn-outline-primary:hover {
  color: white !important;
  background-color: #0052a4 !important;
}

/*change secondary color, other preferences*/
#works .btn-outline-secondary {  
  color: #28a6a7 ;
  border-color: #28a6a7 ;
  border-width:2px !important;
  font-size:18px !important;
  border-radius:20px !important;
  font-weight:500 !important;
  width:119.48px;
  margin-left:5px;
}

/*change secondary color, other preferences*/
#works .btn-outline-secondary:hover {
  color: white ;
  background-color: #28a6a7 ;
}

#works .disable {
  color:gray;
  border-color:gray;
}

#works .disable:hover {
  cursor:not-allowed;
  color:gray;
  border-color:gray;
  background-color:white;
}


/*-----------------------------------------------------------------------------------------------------------------------------*/
/*---------------------------------------------------------------Testimonials--------------------------------------------------*/
/*-----------------------------------------------------------------------------------------------------------------------------*/

/*testimonials*/
#testimonials {
  text-align: center;
  margin-top:36px;
}

/*Testimonials header*/
#testimonials .testimonials-header {
  font-size:36px;
  line-height:45px;
  font-weight:bold;
  margin-bottom:18px;
}

#testimonials .description {
  font-size:18px.25;
  line-height:18px.75;
  margin-bottom:45px;
  width:90%;
  max-width:1000px;
  margin-left:auto;
  margin-right:auto;
}

#testimonials .quote {
  font-size:18px;
  line-height:36px;
  font-family: 'Handlee', cursive;
  font-weight: 600;
}

#testimonials .quote-source {
  font-size:18px;
  font-weight: 800;
  color:#0052a4;
  margin-bottom:13px;
}

#testimonials .quote-title {
  font-size:13px;
  line-height:18px;
  font-weight:300;
  margin-bottom:45px;
}

.testimonials-container {
  margin-bottom:18px;
}

/*-----------------------------------------------------------------------------------------------------------------------------*/
/*---------------------------------------------------------------Call to action------------------------------------------------*/
/*-----------------------------------------------------------------------------------------------------------------------------*/

#call-to-action {
  background-color:#222327;
  border-radius:10px;
  color:white;
  text-align: center;
  margin: auto;
  width:95%;
  max-width:1000px;
  margin-bottom:-72px;
  position:relative;
  z-index:1;
}

#call-to-action .container {
  height:144px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

#call-to-action .col-md-4 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

/*change primary blue to my website blue, other preferences*/
#call-to-action .btn-outline-primary {
  color: white;
  background-color:#222327;
  border-color: #0052a4;
  border-width:2px;
  font-size:18px;
  border-radius:40px;
  font-weight:500;
  width:150px;
}

.call-to-action-description {
  font-size:18px;
  line-height: 36px;
  margin-bottom:0px;
}

/*change primary blue to my website blue*/
#call-to-action .btn-outline-primary:hover {
  color: white;
  background-color: #0052a4;
}

.call-to-action-command {
  font-size:36px;
  font-weight:800;
  margin-bottom:0px;
}

/*-----------------------------------------------------------------------------------------------------------------------------*/
/*---------------------------------------------------------------Bottom--------------------------------------------------------*/
/*-----------------------------------------------------------------------------------------------------------------------------*/

/*foot height, space, color*/
#bottom {
  padding-top:54px+72px;
  background-color: #0052a4;
  text-align:center;
  position:relative;
}

/*ethan vernon ant head*/
#bottom .my-logo {
  width:90vw;
  max-width:300px;
  padding-top:0px;
}

/*space for buttons*/
#bottom .my-links {
  margin: 54px 18px;
  line-height:45px;
}

/*space between buttons*/
#bottom .fa-layers {
  margin: 0px 18px;
}

/*non-hover color for circles*/
#bottom .icon-circles-white {
  color:white;
}

/*hover color for circles*/
#bottom .icon-circles-dark {
  color:#222327;
}

/*non-hover icon color*/
#bottom .icon-inners-dark {
  color:#0052a4;
  margin-left:8px;
}

/*hover color for icons*/
#bottom .icon-inners-white {
  color:white;
  margin-left:8px;
}

/*pointer on hover*/
#bottom .icon-circles:hover, #bottom .icon-inners:hover {
  cursor:pointer;
}

/*less white copyright font*/
#copyright {
  color:#d8d8d8;
  line-height:18px;
  margin-bottom:36px;
}


/*-----------------------------------------------------------------------------------------------------------------------------*/
/*---------------------------------------------------------------Media queries >767px------------------------------------------*/
/*-----------------------------------------------------------------------------------------------------------------------------*/

@media only screen and (min-width: 768px) {
  /*avatar can be a bit bigger on large screens*/
  .avatar {
    height:225px;
  }

  /*bigger for large screen*/
  #title h1 {
    font-size:54px;
    line-height:74px;
  }

  /*cards need uniformity when displayed adjacent*/
  /*fits 2 lines*/
  #works .card-app-name {
    height:80px;
  }
  /*fits 3 lines*/
  #works .card-tech {
    height:108px;
  }
  /*fits 7 lines*/
  #works .card-summary {
    height:252px;
  }
}

/*-----------------------------------------------------------------------------------------------------------------------------*/
/*---------------------------------------------------------------Media queries >992px-----------------------------------------*/
/*-----------------------------------------------------------------------------------------------------------------------------*/

@media only screen and (min-width: 992px) {

  /*bigger for large screen*/
  #title h1 {
    font-size:54px.5;
    line-height:81px;
    margin-bottom:0px
  }  

  #title p {
    font-size:18px.75;
    line-height:36px.75;
    max-width:1000px
  }

  #profile h1 {
    font-size:36px;
    line-height:54px;
  }

  #profile p {
    font-size:18px.25;
    line-height:36px.25;
  }

  /*cards need uniformity when displayed adjacent*/
  /*fits 1 line*/
  #works .card-app-name {
    height:40px;
  }
  /*fits 3 lines*/
  #works .card-tech {
    height:108px;
  }
  /*fits 5 lines*/
  #works .card-summary {
    height:180px;
  }
}

/*-----------------------------------------------------------------------------------------------------------------------------*/
/*---------------------------------------------------------------Media queries <550px------------------------------------------*/
/*-----------------------------------------------------------------------------------------------------------------------------*/

/*formerly 350px*/

@media only screen and (max-width: 550px) {
  /*logo too wide for tiny screens*/
  .my-logo {
    width:140px;
    padding-top:14px;
  }

  .nav-bar-pref {
    height:75px;
    padding-bottom:5px
  }

  .navbar {
    height:100%;
  }

  #title {
    padding-top:75px;
  }

  #title h1 {
    font-size:45px;
    line-height:54px;
    margin-bottom:0px;
  }  

  #title p {
    font-weight:500;
    font-size:18px;
    line-height:27px;
    margin-bottom:18px;
  }

  #profile h1 {
    font-size:36px;
    line-height:45px;
  }

  #profile p {
    font-size:18px;
    line-height:36px;
  }

  .avatar {
    margin-top:0px
  }

  /*button too big*/
  .nav-bar-pref .btn-outline-primary {
    border-width:2px !important;
    font-size:15px !important;
    border-radius:20px !important;
    font-weight:500 !important;
  }

  /*cards don't fit small screens*/
  #works .col {
    padding-left:0px;
    padding-right:0px;
  }

  /*cards don't fit small screens*/
  #works .card-deck{
    width:95%;
    margin-right:auto;
    margin-left:auto;
  }

  /*cards don't fit small screens*/
  #works .card {
    margin-right:auto;
    margin-left:auto;
  }
}

/*-----------------------------------------------------------------------------------------------------------------------------*/
/*---------------------------------------------------------------Media queries <766px------------------------------------------*/
/*-----------------------------------------------------------------------------------------------------------------------------*/

@media only screen and (max-width: 766px) {
  #call-to-action {
    margin-bottom: -124px;
  }

  #call-to-action .container {
    height:auto;
    padding: 18px 15px;
  }

  #call-to-action .col-md-4 {
    margin-bottom:18px;
  }

  #bottom {
    padding-top:54px+124px;
  }
}


/*-----------------------------------------------------------------------------------------------------------------------------*/
/*---------------------------------------------------------------Media queries <680------------------------------------------*/
/*-----------------------------------------------------------------------------------------------------------------------------*/

@media only screen and (max-width: 705px) {
  #works .card-deck {
    margin-right: auto;
    margin-left:auto;
    max-width:500px;
  }
}




